<script>
import dayjs from 'dayjs'

export default {
  data () {
    return {
      name: '',
      idCard: '',
      color: '',
      // time: '',
      // time1: '',
      // time2: '',
      num: null,
      currentTime: '',
      timerId: 0,
      showHeader2: false
    }
  },
  created () {
    this.name = this.$route.query.name
    this.idCard = this.$route.query.idCard
    this.color = this.$route.query.timeColor
    // this.time = this.$route.query.time
    // this.time1 = this.$route.query.time1
    // this.time2 = this.$route.query.time2
    this.num = this.getRandomNum(100, 999)
  },
  mounted () {
    window.addEventListener('scroll', (event) => {
      this.showHeader2 = event.target.documentElement.scrollTop >= 92
    })
    this.currentTime = dayjs().format('MM-DD HH:mm:ss')
    this.timerId = setInterval(() => {
      this.currentTime = dayjs().format('MM-DD HH:mm:ss')
    }, 1000)
  },
  beforeDestroy () {
    clearInterval(this.timerId)
  },
  methods: {
    // 生成从minNum到maxNum的随机数
    getRandomNum (minNum, maxNum) {
      switch (arguments.length) {
        case 1:
          return parseInt(Math.random() * minNum + 1, 10)
        case 2:
          return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10)
        default:
          return 0
      }
    }
  }
}
</script>

